<template>
  <v-dialog :value="show" max-width="600" @input="handleDialogInput">
    <v-card>
      <v-card-title class="headline">Atenção</v-card-title>
      <v-card-text>{{ $store.state.confirmationDialogMessage }}</v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="primary" @click="confirmSave">Salvar e continuar</v-btn>
        <v-btn color="primary" @click="cancel">Cancelar</v-btn>
        <v-btn color="red" class="white--text" @click="confirm"
          >Continuar sem salvar</v-btn
        >
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  props: ["show"],
  methods: {
    confirm() {
      this.$emit("confirm");
    },
    cancel() {
      this.$emit("cancel");
    },
    confirmSave() {
      this.$emit("confirm-save");
    },
    handleDialogInput(open) {
      if (!open) {
        this.cancel();
      }
    },
  },
};
</script>
